import React from 'react'

const FadedText = ({direction, text}) => {
    let class_name = "css--bottom";
    if(direction === 'bottom') {
        class_name = "css--bottom";
    } else if(direction === 'top') {
        class_name = "css--top";
    } else if(direction === 'left') {
        class_name = "css--left";
    } else if(direction === 'right') {
        class_name = "css--right";
    }

    return (
        <div className={`hidden md:block css--fadedtext ${class_name}`}>{text}</div>
    )
}

export default FadedText
