import React, {useLayoutEffect} from "react"
import {graphql, Link} from "gatsby"
import Navigation from "../components/Navigation";
import Img from "gatsby-image";
import FadedText from "../components/helpers/FadedText";
import anime from "animejs";
import {useTranslation} from "react-i18next";
import i18n from "i18next";

export default function BlogPost({data}) {
    const {t} = useTranslation();

    useLayoutEffect(() => {
        let timeline = anime.timeline({
            easing: 'linear',
            autoplay: true,
        });
        timeline.add({
            targets: '.aspect-square',
            easing: 'easeOutExpo',
            opacity: [0, 1],
            scale: [0.8, 1],
            duration: 1000,
            delay: anime.stagger(200),// increase delay by 100ms for each elements.

        });
        timeline.add({
            targets: '.css--fadedtext',
            easing: 'easeOutExpo',
            opacity: [0, 1],
            translateX: ["-100%", 0],
            duration: 500
        });
    }, [])

    return (
        <>
            <Navigation pageNr={'05'}/>
            <FadedText text={'EXPAND'} direction="left" />

            <div className="main-wrapper relative pt-14 min-h-screen">
                <div className="container mx-auto mt-20 md:mt-18">
                    <div className="text-2xl md:text-5xl glitch text-center " data-glitch={t('blogs')}>
                        {t('blogs')}
                    </div>
                    <ul className="grid grid-cols-1 lg:grid-cols-3 gap-2 mt-12" id="news_block">
                        {data.allWpPost.nodes.map(node => (
                            node.language.slug === i18n.language &&

                            <li key={node.slug} className="aspect-square">
                                <Link to={node.slug}>
                                    <div className="col-span-1 flex flex-col h-full absolute w-full leading-8">
                                        <div className="relative h-full">
                                            <Img className="object-cover h-full w-full" fluid={node.featuredImage.node.localFile.childImageSharp.fluid} alt="Blog image"/>
                                            <div className="h-full absolute top-0 w-full p-7">
                                                <div className="col-span-2">
                                                    <h2 className="text-2xl xl:text-4xl text-white font-bold">{node.title}</h2>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </Link>
                            </li>
                        ))}
                    </ul>
                </div>
            </div>
        </>

    )
}

export const pageQuery = graphql`
  query {
    allWpPost(
    sort: { fields: [date] }
    filter: {categories: {nodes: {elemMatch: {name: {eq: "blogs"}}}}}
    ) {
       nodes {
            id
            title
            slug
            excerpt
            date(formatString: "YYYY/MM/DD")
            tags {
              nodes {
                name
              }
            }
            language {
              slug
            }
            featuredImage {
              node {
                sourceUrl
                 localFile {
                   publicURL
                   childImageSharp {
                     gatsbyImageData(
                        placeholder: BLURRED
                     )
                     fluid(maxWidth: 2000, quality: 100) {
                         ...GatsbyImageSharpFluid
                     }
                   }
                 }
              }
            }
          }
    }
  }
`
